import { nextTick } from '../utils';
import { ValidateRule } from '../types';
import template from './text-field.template';
import { RequireRule } from '../constant';

type Props = {
	id: string;
	label: string;
	type: 'text'|'email'|'number';
	placeholder?: string;
	text?: string;
	require: boolean;  //ÌïÑÏàòÏ°∞Í±¥ Ïó¨Î∂Ä ÌôïÏù∏
}

const DefaultProps: Props = {
	id: '',
	text: '',
	label: 'label',
	type: 'text',
	placeholder: '',
	require: false,
}

export default class TextField {
	private template = template;
	private container: string;
	private data: Props;
	private updated: boolean = false; // ÏóÖÎç∞Ïù¥Ìä∏ Ïó¨Î∂Ä (onChange ÌïúÎ≤àÏù¥ÎùºÎèÑ Ïã§ÌñâÌñàÎäîÏßÄ)
	private validateRules: ValidateRule[] = [];

	constructor(container: string, data: Props) {
		this.container = container;
		this.data = {...DefaultProps, ...data }; //default props ÏôÄ Ï†ÑÎã¨Îêú dataÎ•º Î≥ëÌï©, ÏÜçÏÑ±Ïù¥ dataÏóê ÏûàÏúºÎ©¥ Ìï¥Îãπ ÏÜçÏÑ±Í∞íÏúºÎ°ú Î≥ÄÍ≤Ω, ÏóÜÏúºÎ©¥ defaultprops Ïùò Í∞íÏùÑ Ïú†ÏßÄ

		if (this.data.require) { // ÎßåÏïΩ ÏûÖÎ†•Í∞íÏù¥ ÌïÑÏàò Í∞íÏù¥Î©¥ 
			this.addValidateRule(RequireRule); //addValidateRuleÌï®ÏàòÎ•º Ïù¥Ïö©ÌïòÏó¨ ruleÏùÑ ÎÑ£Ïñ¥Ï§ÄÎã§.
		}

		nextTick(this.attachEventHandler);
	}

	private validate = (): ValidateRule | null => {
		const target = this.data.text ? this.data.text.trim() : '';

		const invalidateRules = this.validateRules
			.filter(validateRule => validateRule.rule.test(target) !== validateRule.match);
			// validateRules.rule.test(target)ÏùÑ ÌÜµÌï¥ Í≤∞Í≥ºÏôÄ validateRule.matchÏùò Í∞íÏù¥ ÎèôÏùºÌïòÏßÄ ÏïäÎäî ruleÎßå Î™®ÏïÑ ÏÉàÎ°úÏö¥ Î∞∞Ïó¥Î°ú Î∞òÌôò
			// RegExpÏùò Î©îÏÜåÎìú testÎ•º Ïù¥Ïö©ÌïòÏó¨ target Î¨∏ÏûêÏó¥ÏùÑ ÎçòÏ†Ä Ï†ïÍ∑ú ÌëúÌòÑÏãùÏóê Î∂ÄÌï©ÌïúÏßÄ ÌôïÏù∏ Í≤∞Í≥ºÎ•º true/falseÎ°ú Î∞òÌôò


		return (invalidateRules.length > 0) ? invalidateRules[0] : null;
		// InvalidateRules.length Í∞íÏù¥ 0Î≥¥Îã§ ÌÅ¥Í≤ΩÏö∞ InvalidateRules[0] ÏöîÏÜå Î∞òÌôò, ÏïÑÎãàÎùºÎ©¥ null Î∞òÌôò
	}

	private buildData = () => {
		const isInvalid: ValidateRule | null = this.validate();

		if (this.updated) {
			return {
				...this.data,
				updated: this.updated,
				valid: !isInvalid, // validateÏã§Ìñâ Í≤∞Í≥º (Î∞òÏú†Ìö®ÏÑ±) Í≤ÄÏÇ¨ Í≤∞Í≥º
				validateMessage: !!isInvalid ? isInvalid.message : ''
			}
		} else {
			return {
				...this.data,
				updated: this.updated,
				valid: true,
				validateMessage: ''
			}
		}
	}

	private onChange = (e: Event) => {
		const { value, id } = e.target as HTMLInputElement;
		// target value, id Í∞ÄÏ†∏Ïò®Îã§.

		if (id === this.data.id) {  // ÎßåÏïΩ idÍ∞Ä  this.data.id ÎèôÏùºÌïòÎ©¥
			this.updated = true; // updatedÎ•º trueÎ°ú Î∞îÍæ∏Í≥†
			this.data.text = value; // this.data.textÏóê valueÎ•º ÎÑ£ÎäîÎã§.
			this.update(); // this.update() Ìï®ÏàòÎ•º Ïã§Ìñâ
		}
	}

	private attachEventHandler = () => {
		document.querySelector(this.container)?.addEventListener('change', this.onChange);
		// container Ïóê change Ïù¥Î≤§Ìä∏Í∞Ä Î∞úÏÉùÌïòÎ©¥ this.onChange Ìï®ÏàòÎ•º Ïã§Ìñâ
	}

	private update = () => {
		const container = document.querySelector(`#field-${this.data.id}`) as HTMLElement; // html element Ï§ë #field-${this.data.id}Í∞íÏùÑ Í∞ÄÏßÑ Element ÏöîÏÜåÎ•º ÎÑ£ÎäîÎã§. 
		const docFrag = document.createElement('div'); // divÏöîÏÜåÎ•º ÎßåÎì§Îã§.

		docFrag.innerHTML = this.template(this.buildData()); // buildData Ìï®ÏàòÎ•º Ïã§ÌñâÏãúÏºú Î∞òÌôòÎêú Í≤∞Í≥ºÎ•º templateÏúºÎ°ú Ï†ÑÎã®ÌïúÎã§.
		container.innerHTML = docFrag.children[0].innerHTML; //docFragÏùò ÏûêÏãù ÏöîÏÜåÏ§ë 0Î≤àÏß∏Ïùò html ÏöîÏÜåÎ•º Í∞ÄÏ†∏ÏôÄ container Ïóê innerHTML Ïù¥Ïö©ÌïòÏó¨ ÏÇΩÏûÖ
	}

	public get name(): string {
		return this.data.id;
	}

	public get value(): string {
		return this.data.text || '';
	}

	public get isValid(): boolean {
		return !this.validate();
	}

	public addValidateRule = (rule:ValidateRule) => {
		this.validateRules.push(rule);
	}

	public render = (append: boolean = false) => {
		const container = document.querySelector(this.container) as HTMLElement;

		if (append) {
			const divFragment = document.createElement('div');
			divFragment.innerHTML = this.template(this.buildData());

			container.appendChild(divFragment.children[0]);
		} else {
			container.innerHTML = this.template(this.buildData());
		}
	}
}